.works {
    padding: 0 2em 0 2em;
    display: flex;
    height: 90vh;
    margin-top: 4em;
}

.w-right {
    position: relative;
}

.w-mainCircle {
    position: relative;
    left: 10em;
    width: 20em;
    height: 20em;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    /* border: 1px solid black; */
    top: 2em;
    background: white;
}
.w-mainCircle * {
    position: absolute;
    transform: scale(.8);
    
}
.w-mainCircle :nth-child(1) {
    top: -2em;
    left: 7em;
}
.w-mainCircle :nth-child(2) {
    top:7em;
    left: -2.5em;
}
.w-mainCircle :nth-child(3) {
    top: 7em;
    left: 7em;
}
.w-mainCircle :nth-child(4) {
    top: 6em;
    left: 16em;
}
.w-mainCircle :nth-child(5) {
    top: 16em;
    left: 7em;
}

.w-secCircle {
    width: 6em;
    height: 6em;
    border: 4px solid rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    background: white;   
    box-shadow: var(--smboxShadow);
    
}
.w-secCircle :nth-child(1) {
    position: absolute;
    top: 1em;
    left: 0em;
}

.w-backCircle {
    position: absolute;
    height: 10em;
    width: 10em;
    border-radius: 100%;
    z-index: -1;

}
.blueCircle {
    background: blue;
    left: 22em;
    top: 4em;
}
.yellowCircle {
    background: yellow;
    left: 22em;
    top: 10em;
}

@media screen and (max-width:480px) {
    .works {
        flex-direction: column;
        height: 50em;
        padding: 0;
        gap: 5em;
    }
    .w-right {
        /* display: flex; */
        align-items: flex-start;
        justify-content: start;
        transform: scale(.7);
    }
    .w-mainCircle {
        position: static;
        
    }
    .w-backCircle  {
        display: none;
    }
    
}
@media screen and (max-width:725px) {
    .works {
        flex-direction: column;
        height: 45em;
        padding: 0;
        gap: 5em;
    }
    .w-right {
        /* display: flex; */
        align-items: flex-start;
        justify-content: start;
        transform: scale(.9);
    }
    .w-mainCircle {
        position: static;
    }
    .w-backCircle  {
        display: none;   
    }
    
}
