.toggle {
    display: flex;
    justify-content: space-between;
    border: 3px solid var(--orange);
    border-radius: 1em;
    position: relative;
    padding: 2px;
    cursor: pointer;
}
.toggle * {
    width: 1em;
    height: 1em;
    color: var(--orange);
}
.t-button {
    border-radius: 100%;
    background: var(--orange);
    position: absolute;
}