:root
{
  --yellow: green;
    --orange : #6AAD06;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: #6AAD06;
    --smboxShadow: -7px 8px 5px rgba(0, 0, 0, 0.3);
}

@font-face {
  font-family: FiraSans;
  src: url(./fonts/FiraSans-Regular.otf);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: var(--black);  
    font-family: 'FiraSans';  
}

.button {
    background: linear-gradient(180deg, #ACFF2F 26.71%, #6AAD06 99.36%);
    border-radius: 14px;
    border: none;
    color: var(--black);
    font-size: 16px;
    padding: 11px 26px 11px 26px;
    width: 10em;
    height: 2.5em;
    font-weight: 600;
  }
  
  .button:hover {
  box-shadow: 0px 4px 5px 1px #ACFF2F;
  background: white;
  cursor: pointer;
  border: 1px solid var(--orange);
  color: var(--orange);
  color: #6AAD06;
  }
  @media screen and (max-width: 480px) {
    .App {
      padding: 0.5rem 1rem;

    }
  }
* {
  margin: 0;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  font-family: 'FiraSans';
}
  

  