@media (width > 550px) {
    .burger {
        display: none;
    }
}
@media (max-width:250px) {
    .burger {
        display: none;
    }
}

@media (width <=550px) {
    .burger img {
        position: fixed;
        right: 5%;
        top: 3.5%;
        background: #5D9706;
        border-radius: .4rem;
        cursor: pointer;
        z-index: 6;
    }

}