
@media (min-width:551px) {
    
    .sidebar ul{
        display: none;
    }
    .sidebar .burger {
        display: none;
    }
    .copy {
        display: none;
    }
}
@media (max-width:250px) {
    .sidebar {
        display: none;
    }
    .copy {
        display: none;
    }
}

@media (max-width:550px) {
.sidebar {
    /* display: none; */
    position: fixed;
    background: #6AAD06;
    width: 200px;
    height: 100vh;
    right: 0%;
    top: 0%;
    z-index: 5;
    transform: translateX(100%);
    transition: all .2s linear;
}
.sidebar ul {
    position: absolute;
    top: 10%;
    right: 10%;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.sidebar ul li {
    border: 1px solid #fff;
    background: #fff;
    border-radius: 1rem;
    padding: .3rem 2rem;
    margin: 1rem auto;
    cursor: pointer;
    transition: all .2s linear;
}
.sidebar ul li:hover {
 background: transparent;   
 color: #fff;
}
.sidebar ul li a{
    line-height: 2rem;
    font-size: 1rem;    
}

.sidebar--open {
    transform: translate(0%);
}
    
}